<template>
  <div>
    <ul id="pclist" ref="divs">
      <li ref="div" @click="add(item,index)" class="item" :class="{'remove':item==0}" v-for="(item,index) in itemlist"
          :style="'background-position:-'+px(item)+'rem -'+py(item)+'rem;'" :index="item">{{item}}</li>
      <li class="fn-clear"></li>
    </ul>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        itemlist: [0,1,2,3,4,5,6,7,8],
        rows:3,
        cols:3,
        width:15,
        height:15
      }
    },
    created(){
      this.$nextTick(()=>{

      })

    },
    methods:{
      px:function (index) {
        return (index%this.cols)*(this.width/this.cols);
      },
      py:function (index) {
        return parseInt(index/this.cols)*(this.height/this.rows)
      },
      //row 行
      getrows:function (index) {
        return parseInt(index/this.cols)
      },
      getcols:function (index) {
        return index%this.cols
      },
      getBound:function (index) {
        var left = index-1;
        var right = index+1;
        var top = index - this.cols;
        var bottom = index + this.cols;
        var len = this.itemlist.length;
        var row = this.getrows(index);
        var col = this.getcols(index);
        var ra = new Array();

        if(left>=0 && left<len && this.getrows(left)==row){

            ra.push(left)
        }
        if(right>=0 && right<len && this.getrows(right)==row){

          ra.push(right)
        }
        if(top>= 0 && top<len && this.getcols(top)==col){

          ra.push(top)
        }
        if(bottom >= 0 && bottom<len && this.getcols(bottom)==col){

          ra.push(bottom)
        }
        return ra
      },
      box_swap(i,j,item){

//        var iobj = this.$refs.div[i];
//        var jobj = this.$refs.div[j];
//        var tobj = jobj.cloneNode(true)
//        console.log(this.$refs.divs)
//        this.$refs.divs.insertBefore(tobj,iobj)
//        jobj.remove()
//        console.log(iobj)
//        console.log(jobj)
//        console.log(tobj)

//        this.itemlist.splice(i,1,j)
//        this.itemlist.splice(j,1,0)

          this.itemlist.splice(i,1,item)
          this.itemlist.splice(j,1,0)


      },
      add:function (item,index) {

//        console.log(item);
//       console.log(index);
//        console.log(this.getBound(index));

        var cindex = index;

        var oindex="";
        this.$refs.div.forEach((i,index)=>{

          if(i.className=="item remove"){

            oindex = index;

          }

        })
        var oa = this.getBound(index)
        oa.forEach((i,index)=>{
          if(i==oindex){
            this.box_swap(oindex,cindex,item)
            console.log(oindex);
            console.log(cindex);
            console.log(item);
          } else{
            return false
          }
        })
       console.log(this.itemlist.join(','))
        if(this.itemlist.join(',')=="0,1,2,3,4,5,6,7,8"){
          alert('ok')
        }
      }
    }
  }
</script>
<style>
 #pclist{
   width: 15rem;height: 15rem;background-color: green;
 }
  .item{
    width: 5rem;height: 5rem;background: url(../assets/images/2.jpg) 0px 0px no-repeat ;float: left;
    background-size: 15rem 15rem;
    font-size: 33px;color:red;
    cursor: pointer;
  }
  .fn-clear{
    clear: both;height: 0rem;line-height: 0rem;
  }
  .remove{
    background: none;
    background-color:green;
  }
</style>



