<template>
  <div class="sHeader">
    <div v-if="showHeader">
      <p>头部{{headerText}}</p>

    </div>
  </div>
</template>

<script>
  export default {
    props: {},
    data() {
      return {
        headerText:'',
        showHeader: true
      }
    },
    created(){
      this.$bus.on('headerText',(text)=>{
        this.headerText = text;

      })

    }
  }
</script>

<style scoped>
  .top-header {
    z-index: 62 !important;
  }

  .abs-top {
    position: absolute !important;
    top: -.96rem;
    max-width: 768px;
  }

  .sHeader {
    position: fixed;
    z-index: 63 !important;
    top: 3.5rem;
    right: 0;
    width: 100%;
    font-size: 0;
    height: .96rem;
    line-height: .96rem;
    -webkit-text-size-adjust: none;
    text-align: right;
    background: red;
  }

  .sharelist {
    display: inline-block;
    height: .96rem;
    min-width: .75rem;
    width: 100%;
    text-indent: -9999px;
    background-size: .49rem;
  }

</style>



