import sHeader from './isHeader.vue'
import kugou from './kugou/kugou.vue'
import xingge from './kugou/xingge.vue'
import plist from './kugou/plist.vue'
import search from './kugou/search.vue'
import pintu from './kugou/pintu.vue'
export const kugous =
  {
    path: '/kugou',
    components:{
      default:kugou
    },
    children: [
      {
        path: 'store/:state',
        component: xingge,
        name: 'kugou',
      },
      {
        path: 'plist',
        component: plist,
        name: 'plist',
      },
      {
        path: 'search',
        component: search,
        name: 'searchlist',
      },
      {
        path: 'pintu',
        components: {
          default:pintu,
          'dl-header':sHeader
        },
        name: 'pintu',
      }
    ]
  }



